<template>
	<view class="page">
		<view class="input-t">
			<text>状态</text>
			<text class="input-quick" >{{repairDetail.classroomStatus === '0' ? '可用' : '已锁定'}}</text>
		</view>

		<button class="confirm-btn" :class="'bg-' + themeColor.name" :disabled="btnLoading" :loading="btnLoading"
			@tap="sendUp()">
			{{repairDetail.classroomStatus === '0' ? '锁定' : '解锁'}}
		</button>
		

	</view>
</template>

<script>
	import moment from '@/common/moment';
	export default {
		data() {
			return {
				repairDetail:{},
				date: '',
				type: '',
				mtype: '',
				mtypeValue: '', // 选中下标
				sendDate: {
					remind: '0',
					content: ''
				},
				form: {
					id: '',
					classroomStatus: '',
					lockUserId: uni.getStorageSync('userInfo').userId,
					lockUserName: uni.getStorageSync('userInfo').userName,
					lockUserPhone: uni.getStorageSync('userInfo').phonenumber,
				},
				worksType: [],
				worksTypeList: [],
				btnLoading: false
			};
		},
		onLoad(e) {
			this.repairDetail = JSON.parse(e.item)
			uni.setNavigationBarTitle({
			    title: '解锁教室'
			});
			this.form.id = this.repairDetail.id
			this.form.classroomName = this.repairDetail.classroomName
			this.form.classroomStatus = this.repairDetail.classroomStatus === '0' ? '1' : '0'
		},
		onShow() {
			uni.setNavigationBarColor({
				frontColor: '#ffffff',
				backgroundColor: this.themeColor.color,
				animation: {
					duration: 400,
					timingFunc: 'easeIn'
				}
			})
		},

		methods: {
			
			sendUp(){
				let that = this;
				this.btnLoading = true
				uni.showLoading({
					title: '操作中...'
				});
				setTimeout(() => {
					that.btnLoading = false
				}, 3000)
				that.ajax(that.url.editClassRoomInfo, 'POST', that.form, function(resp) {
					if (resp.data.code === 200) {
						uni.showToast({
							title: resp.data.msg,
							icon: 'success',
							complete() {
								setTimeout(() => {
									let pages = getCurrentPages(); //获取所有页面栈实例列表
									let nowPage = pages[pages.length - 1]; //当前页页面实例
									let prevPage = pages[pages.length - 2]; //上一页页面实例
									prevPage.$vm.type = that.type; //修改上一页data里面的type参数值为that.type
									uni.navigateBack({ //uni.navigateTo跳转的返回，默认1为返回上一级
										delta: 1,
										success: function() {
											//prevPage.onLoad(); // 执行上一页的onLoad方法
										}
									});
								}, 500)
							}
						})
						uni.hideLoading();
						that.btnLoading = false
				
					} else {
						uni.showToast({
							title: resp.data.msg,
							icon: 'none',
						})
						uni.hideLoading();
						that.btnLoading = false
					}
				
				});
				
			},
			

		}
	};
</script>

<style lang="scss">
	page {
		background-color: $page-color-base;
	}

	.date {
		height: 80upx;
		line-height: 80upx;
		font-size: $font-base + 2upx;
		color: $font-color-dark;
	}

	/*表单标题*/
	.input-t {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: $spacing-base;
		margin-top: $spacing-base;
		font-size: $font-base;

	}

	.input-quick {
		position: relative;
		padding-right: 40upx;

		.iconfont {
			font-size: $font-sm;
		}
	}

	.input-body {
		background: $color-white;
		padding: $spacing-sm $spacing-base;

		.gender-item {
			margin-right: 20upx;

			.gender-item-text {
				padding-left: 10upx;
			}

		}
	}

	.input-textare {
		height: 200upx;
		font-size: 34upx;
		line-height: 50upx;
		width: 100%;
		box-sizing: border-box;
		padding: 20upx 30upx 0;
	}
</style>
